<style lang="scss">
.footer-container {
    background-image: url("@/assets/contact-bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    
    .footer-content {
        padding: 35px 5%;
        border-top-left-radius: 95px;
        border-top-right-radius: 95px;
        background: #222222;
        border: solid 2px #216a6e;
        border-bottom: none;
        display: flex;
        justify-content: center;

        .footer-top {
            display: flex;
            justify-content: space-between;

            .footer-links {
                display: flex;
                flex-direction: column;
                align-items: center;

                a {
                    font-family: "FuturaBook";
                    font-size: 20px;
                    font-weight: 400;
                    line-height: 24px;
                    letter-spacing: 0em;
                    text-align: left;
                    color: #ffffff;
                    text-decoration: none;
                    transition: transform 0.5s ease;

                    &:hover {
                        text-decoration: underline;
                        transform: scale(1.1);
                    }
                }
            }

            .footer-social {
                display: flex;
                gap: 20px;
            }
        }
    }

    .footer-bottom {
        background: #222222;
        display: flex;
        justify-content: center;

        .footer-bottom-content {
            display: flex;
            align-items: center;
            justify-content: space-evenly;
            padding-bottom: 32px;

            a {
                color: #21bec5;
                font-family: "Montserrat";
                text-decoration: none;
                transition: transform 0.5s ease;

                &:hover {
                    text-decoration: underline;
                    transform: scale(1.1);
                }
            }
        }
    }

    @media (max-width: 1200px) {
        .footer-content {
            .footer-top {
                flex-direction: column;
                align-items: center;
                gap: 32px;
            }
        }
        .footer-bottom {
            .footer-bottom-content {
                flex-direction: column;
                gap: 6px;
            }
        }
    }
}
</style>
